import { computed, onMounted, ref } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import { ElMessageBox } from 'element-plus';
import fadeIn from '@/hooks/fadeIn';
export default {
  name: 'HomeMain',

  setup() {
    const router = useRouter();
    const store = useStore();
    const hasResume = computed(() => {
      return store.state.step1Data.name ? true : false;
    });
    const btnGroup = ref();

    const removeClass = () => {
      for (let index = 0; index < btnGroup.value.children.length; index++) {
        const element = btnGroup.value.children[index];
        element.classList.remove("active");
        element.classList.remove("inactive");
      }
    };

    onMounted(() => {
      if (!localStorage.getItem('hiddenAppTips') && !sessionStorage.getItem('tHiddenAppTips')) {
        ElMessageBox.confirm('<br><strong></strong><br><strong></strong>pdf(,pdf)<br><a style="color: #409EFF;text-decoration: underline;" href="mailto:32413082@qq.com">32413082@qq.com</a>', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          draggable: true,
          dangerouslyUseHTMLString: true,
          distinguishCancelAndClose: true
        }).catch(status => {
          if (status === 'cancel') {
            localStorage.setItem('hiddenAppTips', 'true');
          }
        }).finally(() => {
          sessionStorage.setItem('tHiddenAppTips', 'true');
        });
      }

      for (let i = 0; i < btnGroup.value.children.length; i++) {
        const element = btnGroup.value.children[i];
        element.addEventListener("mouseenter", () => {
          element.classList.add("active");

          for (let j = 0; j < btnGroup.value.children.length; j++) {
            const element1 = btnGroup.value.children[j];

            if (i !== j) {
              element1.classList.add("inactive");
            }
          }
        });
      }

      fadeIn();
    });

    const newResume = () => {
      router.push('/edit/step1');
    };

    const fullscreenLoading = ref(false);

    const exResume = () => {
      fullscreenLoading.value = true;
      setTimeout(() => {
        fullscreenLoading.value = false;

        if (hasResume.value) {
          router.push('/exportResume');
        } else {
          ElMessageBox.confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            router.push('/edit/step1');
          }).catch(() => {});
        }
      }, 1000);
    };

    return {
      btnGroup,
      hasResume,
      removeClass,
      newResume,
      fullscreenLoading,
      exResume
    };
  }

};